import { _decorator, Component, AudioSource, AudioClip, Node, Prefab, instantiate } from 'cc';
const { ccclass, property } = _decorator;

import { SFXID } from "./SFXID";
import { SFXEntry } from "./SFXEntry";

@ccclass('SFXManager')
export class SFXManager extends Component {
    @property({ tooltip: 'Use pooling for SFX playback' })
    usePooling: boolean = false;

    @property({
        visible() {
            return !this.usePooling;
        }
    })
    singleSFXSource: AudioSource = null!;

    @property({
        visible() {
            return this.usePooling;
        }
    })
    sfxAudioSourcePrefab: Prefab = null!;

    @property({ type: [SFXEntry] })
    sfxEntries: SFXEntry[] = [];

    private clipMap: Map<SFXID, AudioClip> = new Map();

    private sfxPool: Node[] = [];
    private maxPoolSize = 10;

    onLoad() {
        this.sfxEntries.forEach(entry => {
            this.clipMap.set(entry.id, entry.clip);
        });
        if (this.usePooling) {
            for (let i = 0; i < this.maxPoolSize; i++) {
                const node = instantiate(this.sfxAudioSourcePrefab);
                node.setParent(this.node);
                node.active = false;
                this.sfxPool.push(node);
            }
        }
    }

    public playSFXByID(id: SFXID, volume = 1.0) {
        const clip = this.clipMap.get(id);
        if (clip) {
            this.playSFX(clip, volume);
        } else {
            console.warn(`[SFXManager] Clip not found: ${id}`);
        }
    }
    
    public playSFX(clip: AudioClip, volume = 1.0) {
        if (!this.usePooling) {
            this.singleSFXSource.playOneShot(clip, volume);
            return;
        }

        const sourceNode = this.sfxPool.find(n => !n.getComponent(AudioSource).playing);
        if (sourceNode) {
            const audio = sourceNode.getComponent(AudioSource);
            sourceNode.active = true;
            audio.clip = clip;
            audio.volume = volume;
            audio.play();
        } else {
            console.warn('[SFXManager] No available SFX source in pool');
        }
    }

    public setVolume(volume: number) {
        if (!this.usePooling) {
            this.singleSFXSource.volume = volume;
        } else {
            this.sfxPool.forEach(node => {
                node.getComponent(AudioSource).volume = volume;
            });
        }
    }
}